import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button, Container, Row, Col, Modal } from "react-bootstrap";
import { TelephoneFill } from "react-bootstrap-icons";
import { faUsers } from "@fortawesome/free-solid-svg-icons";
import Logo from "../assets/wwbm.png";
import { useEffect, useState } from "react";

import "../styles/game.css";
import CustomQuestions from "./customQuestions";

function Game() {
   const money = [
      "$ 1 000 000",
      "$ 500 000",
      "$ 250 000",
      "$ 64 000",
      "$ 32 000",
      "$ 16 000",
      "$ 8 000",
      "$ 4 000",
      "$ 2 000",
      "$ 1 000",
      "$ 500",
      "$ 300",
      "$ 200",
      "$ 100",
   ];

   const [data, setdata] = useState([]);
   const [currentQuestion, setCurrentQuestion] = useState(0);
   const [moneyLadder, setMoneyLadder] = useState(15);
   const [moneyLadderMobile, setmoneyLadderMobile] = useState(false);
   const [inCorrect, setinCorrect] = useState();

   const shuffle = (arr) => arr.sort(() => Math.random() - 0.5);
   const questionsArr = [];

   useEffect(() => {
      const fetchData = () => {
         return fetch(
            "https://opentdb.com/api.php?amount=50&category=9&easy=medium&type=multiple"
         )
            .then((response) => response.json())
            .then((data) => {
               let results = data.results;
               results.forEach((item) => {
                  let questions = {
                     question: item.question,
                     options: shuffle([
                        ...item.incorrect_answers,
                        item.correct_answer,
                     ]),
                     answer: item.correct_answer,
                     inCorrectOption: shuffle([...item.incorrect_answers]),
                  };
                  questionsArr.push(questions);

                  // setinCorrect(inCorrectOption);
                  // console.log(inCorrectOption[currentQuestion]);
               });

               setdata(shuffle(questionsArr));
            });
      };
      fetchData();
   }, []);

   const fiftyFifty = () => {
      data[currentQuestion].inCorrectOption.splice(0, 2);
   };

   const [className, setclassName] = useState("");
   const [disable, setDisable] = useState("");
   const [show, setShow] = useState(false);
   const [show2, setShow2] = useState(false);
   const [height, setHeight] = useState();

   const callFriendClose = () => setShow(false);
   const callFriendShow = () => {
      setTimeout(() => {
         setShow(true);
      }, 2000);
      setclassName("disabled");
   };
   const askAudienceShow = () => {
      setTimeout(() => {
         setShow2(true);
      }, 2000);
      setDisable("disabled");
   };
   const askAudienceClose = () => setShow2(false);

   const randomNumber = () => {
      data[currentQuestion].options.map((option) => {
         if (data[currentQuestion].answer === option) {
            setHeight(Math.floor(Math.random() * (100 - 80) + 80));
         }
      });
      randomNumber();
   };

   return (
      <>
         <div className="gameMain">
            <div className="gameSub">
               {data[currentQuestion] && (
                  <div className="QandA">
                     <div className="holder">
                        <div className="logoDiv">
                           <img src={Logo} alt="logo" width="200" />
                        </div>
                        <div className="lifeTime">
                           <Button className="" onClick={fiftyFifty}>
                              50/50
                           </Button>

                           <Button
                              disabled={className}
                              onClick={callFriendShow}
                           >
                              <TelephoneFill />
                           </Button>
                           <Modal
                              show={show}
                              onHide={callFriendClose}
                              backdrop="static"
                              keyboard={false}
                              centered
                           >
                              <Modal.Body className="callAfriend">
                                 <h5
                                    className="mb-5"
                                    dangerouslySetInnerHTML={{
                                       __html: `Hello Friend!! The right answer is "${data[currentQuestion].answer}"`,
                                    }}
                                 />
                                 <Button
                                    style={{
                                       float: "right",
                                       color: "black",
                                       backgroundColor: "rgb(255, 166, 0)",
                                    }}
                                    onClick={callFriendClose}
                                 >
                                    Close
                                 </Button>
                              </Modal.Body>
                           </Modal>

                           <Button onClick={askAudienceShow}>
                              <FontAwesomeIcon icon={faUsers} />
                           </Button>
                           <Modal
                              show={show2}
                              onHide={askAudienceClose}
                              backdrop="static"
                              keyboard={false}
                              centered
                           >
                              <Modal.Body className="callAfriend">
                                 <Container className="mb-3">
                                    <Row>
                                       <Col>
                                          <div></div>
                                          <h5></h5>
                                          <h5>1</h5>
                                       </Col>
                                       <Col>
                                          <div></div>
                                          <h5></h5>
                                          <h5>2</h5>
                                       </Col>
                                       <Col>
                                          <div></div>
                                          <h5></h5>
                                          <h5>3</h5>
                                       </Col>
                                       <Col>
                                          <div></div>
                                          <h5></h5>
                                          <h5>4</h5>
                                       </Col>
                                    </Row>
                                 </Container>
                                 <Button
                                    style={{
                                       float: "right",
                                       color: "black",
                                       backgroundColor: "rgb(255, 166, 0)",
                                    }}
                                    onClick={askAudienceClose}
                                 >
                                    Close
                                 </Button>
                              </Modal.Body>
                           </Modal>
                        </div>
                     </div>

                     <div className="questionDiv">
                        <div>
                           <div className="mt-5">
                              <div className="question">
                                 <h5
                                    dangerouslySetInnerHTML={{
                                       __html: data[currentQuestion].question,
                                    }}
                                 />
                              </div>
                           </div>
                           <div className="answers">
                              {data[currentQuestion].options.map(
                                 (option, index) => (
                                    <CustomQuestions
                                       data={data}
                                       option={option}
                                       index={index}
                                       setMoneyLadder={setMoneyLadder}
                                       moneyLadder={moneyLadder}
                                       setmoneyLadderMobile={
                                          setmoneyLadderMobile
                                       }
                                       setCurrentQuestion={setCurrentQuestion}
                                       currentQuestion={currentQuestion}
                                    />
                                 )
                              )}
                           </div>
                        </div>
                        {currentQuestion === 14 &&
                           (alert(
                              "Congratulations!! You just won 1 million naira"
                           ),
                           setCurrentQuestion(0),
                           data[currentQuestion],
                           setMoneyLadder(15),
                           window.location.reload())}
                     </div>
                  </div>
               )}

               <div className="money">
                  <div>
                     {money.map((money, index) => (
                        <h5
                           key={index}
                           className={
                              moneyLadder === index + 1
                                 ? "bg-warning text-white p-1"
                                 : ""
                           }
                        >
                           {money}
                        </h5>
                     ))}
                  </div>
               </div>
            </div>
            {moneyLadderMobile === true && (
               <div className="moneyMobile">
                  {money.map((money, index) => (
                     <h5
                        key={index}
                        className={
                           moneyLadder === index + 1
                              ? "bg-warning text-white p-1"
                              : ""
                        }
                     >
                        {money}
                     </h5>
                  ))}
               </div>
            )}
         </div>
      </>
   );
}

export default Game;
